	import navbar from '@/components/navbar/navbar.vue'
	export default {
		components: {
			navbar
		},
		data() {
			return {
			  //验证短信验证码接口参数
               codeParams:{
				   mobile:"",
				   code:"",
				   tag:""
			   },
			   isShowEye:false,//是否显示密码
			   isCaptcha:true,
			   second:120,
			}
		},
		methods:{
			//验证方法
			checkDatalogin() {
				if (!this.codeParams.mobile) {
					uni.showToast({
						title: '手机号码不能为空',
						icon: 'none',
						mask: true,
						duration: 1000,
					});
					return false
				};
				if (!this.codeParams.code) {
					uni.showToast({
						title: '验证码不能为空',
						icon: 'none',
						mask: true,
						duration: 1000,
					});
					return false
				};
				if (!this.codeParams.tag) {
					uni.showToast({
						title: '新密码不能为空',
						icon: 'none',
						mask: true,
						duration: 1000,
					});
					return false
				};
				return true
			},
			//发送验证码
			sendCode(){
				// console.log(666)
				if(!(/^(\+\d{2,3}-)?(\d{2,3}-)?(\d{2,16})$/).test(this.codeParams.mobile) || this.codeParams.mobile.length<0){
					uni.showToast({
					       title: "请输入正确的手机号码",
						   icon: 'none',
					       mask: true,
					       duration: 1000,
					});
					return;
				} 
				// else	{
					
					this.$util.get("/baseService/sms/smsCaptcha/"+this.codeParams.mobile+"/trace",).then(res => {
						console.log(666)
						if (Number(res.data.statusCode) === 200) {
							
						  this.isCaptcha = false;
						    // 设计倒计时
						   let timerId = setInterval(() => {
						         --this.second;
						   },1000)
						         //清除倒计时
						  setTimeout(() => {
						                 clearInterval(timerId);
						                 this.isCaptcha = true;
						                 this.second = 120;  // 重新变成120s
						   },120000)  
							
					      uni.showToast({
					            title: res.data.statusMsg,
					            icon: 'none',
					            mask: true,
					            duration: 1000,
					     });
						 
						} else {
					       uni.showToast({
					                        title: res.data.statusMsg,
					                        icon: 'none',
					                        mask: true,
					                        duration: 1000,
					       })  
						}
					});
					
					
				// }
				   
					 
									

			},
			/**
			 * @desc 密码显隐操作
			 */
			passwordClick() {
				this.isShowEye = !this.isShowEye;
			},
			verificationCode(){
				if(this.checkDatalogin() ){
					let _param =  {
					         mobile: this.codeParams.mobile,
					         code: this.codeParams.code,
							 tag:"trace"
					  };
					  // 验证短信验证码接口
					 this.$util.get("/baseService/sms/smsCaptcha/"+_param.mobile+"/"+_param.code+"/"+_param.tag,).then(res => {
						if (Number(res.data.statusCode) === 200) {
						let param =  {
						         accountName: this.codeParams.mobile,
						         password: this.codeParams.tag,
						 };	
							
						this.$util.httpPost("/accountCenter/account/resetAccountPassword" , param).then(res => {
							if (Number(res.data.statusCode) === 200) {
								uni.showToast({
									title: '修改密码成功',
									icon: 'success',
									mask: true,
									duration: 1000,
								});
								//修改密码成功后三秒跳转登录页
								setTimeout(() => {
								  uni.navigateTo({
								  	url:"../login/login"
								  })
								 },3000)   

							} else {
								uni.showToast({
									title: res.data.statusMsg,
									icon: 'none',
									mask: true,
									duration: 1000,
								})
							}
						});	
						} else {
					       uni.showToast({
					                        title: res.data.statusMsg,
					                        icon: 'none',
					                        mask: true,
					                        duration: 1000,
					       })  
						}
					});
					
				}
								
				
			}
		}

	}
